.about{
    width: 90%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}
.about .left{
    align-items: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;

}
.about .left p{
    margin: 1.2rem 0;
}
.about .right{
    max-width: 500px;
}
.right .img-container{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;
}
.right .img-container img{
    max-width: 100%;
    border: 1px solid #fff;
}
.img-top {
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 70%;
    z-index: 6;   
}
.img-buttom {
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;
    
}
